.NavigationItem {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  margin: 10px 0;
  width: 100%;
}

.NavigationItem a {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #8f5c2c;
  display: block;
  text-decoration: none;
  width: 100%;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
  color: #40A4C8;
}

@media (min-width: 500px) {
  .NavigationItem {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    margin: 0;
    width: auto;
  }
  
  .NavigationItem a {
    border-bottom: 4px solid transparent;
    color: white;
    height: 100%;
    padding: 16px 10px;
  }
  
  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active {
    background-color: #8f5c2c;
    border-bottom: 4px solid #40A4C8;
    color: white;
  }
}



