.Burger {
  width: 100%;
  margin: auto;
  height: 250px;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}

.Burger p {
  padding-bottom: 1rem;
  padding-top: 1rem;
}

@media (min-width: 500px) and (min-height: 400px) {
  .Burger {
    height: 200px;
    width: 200px;
  }
}

@media (min-width: 500px) and (min-height: 401px) {
  .Burger {
    height: 300px;
    width: 300px;
  }
}

@media (min-width: 1000px) and (min-height: 700px) {
  .Burger {
    height: 500px;
    width: 500px;
  }
}



