.Toolbar {
  -ms-flex-align: center;
      align-items: center;
  background-color: #eeeeee;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  height: 56px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  left: 0;
  padding: 0 20px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 90;
}

.Toolbar nav {
  height: 100%;
}

/* .Logo {
  height: 80%;
} */

@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}



